import React, { Component } from 'react';
import { Button, Panel, Grid, Glyphicon, Row, Col, Image, Thumbnail, Modal } from 'react-bootstrap';
import DaliPortrait from '../assets/dali-portrait.png';
import BotticelliPortrait from '../assets/botticelli-portrait.png';

export default class ArtistModals extends Component {
  state = {
    showDaliModal: false,
    showBotticelliModal: false,
  }

  toggleDaliModal = () => this.setState({ showDaliModal: !this.state.showDaliModal})
  toggleBotticelliModal = () => {
    console.log("TOG");
    console.log(this.state.showBotticelliModal);
    this.setState({ showBotticelliModal: !this.state.showBotticelliModal})
  }

  render() {
    return (
      <div>
        {/* MODALS */}
        <Modal show={this.state.showDaliModal} onHide={this.toggleDaliModal}>
            <Modal.Header closeButton>
              <Modal.Title><h3>Salvador Dali (1904-1989)</h3></Modal.Title>
            </Modal.Header>
            <Modal.Body>
              <Panel>
                <Panel.Heading><b>Life</b></Panel.Heading>
                <Panel.Body>
                  Born to an affluent family, Dali engaged his passion for art early and aggressively. Indeed,
                  in his autobiography, Dali recalls painting atop a tower in
                  his house, nude, screaming at the pedestrians below. These idiosyncratic
                  behaviors certainly manifest in his art. Significantly, he also had mixed religious messages:
                  his mother was a devout Catholic, while his father
                  an atheist. His own beliefs flitted between those two extremes.
                </Panel.Body>
              </Panel>
              <Panel>
                <Panel.Heading><b>Art</b></Panel.Heading>
                <Panel.Body>Dali is widely recognized as a pioneer of the surrealist style.
                  Works such as "The Trachery of Images" and "The Persistence of Memory"
                encourage his viewers to question our reality. Dali's figures are often amorphous,
                melting blobs.</Panel.Body>
              </Panel>
              <Panel>
                <Panel.Heading><b>Divine Comedy</b></Panel.Heading>
                <Panel.Body>
                  Throughout his lifetime, Dali illustrated several books and poems.
                However, his representations of Dante's Divine Comedy (particularly Inferno)
              escape comprehension. The Italian Government commissioned him in the 1950s
              to transform Dante's magnum opus to pictoral representations. Over the course of several years,
              he illustrated the entire Divine Comedy. We will soon see how
              those turned out...
            </Panel.Body>
              </Panel>
            </Modal.Body>
            <Modal.Footer>
              <Button onClick={this.toggleDaliModal}>Close</Button>
            </Modal.Footer>
          </Modal>

          <Modal show={this.state.showBotticelliModal} onHide={this.toggleBotticelliModal}>
              <Modal.Header closeButton>
                <Modal.Title><h3>Sandro Botticelli (1445-1510)</h3></Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <Panel>
                  <Panel.Heading><b>Life</b></Panel.Heading>
                  <Panel.Body>
                    Botticelli was a Florentine early-renaissance painter. His works span subjects
                    from Christian themes to Greek myths. However, he was an extremely devout Catholic, especially
                    later in his life. At one point, he burned one of his own mythological paintings,
                    because of its pagan inspiration.
                  </Panel.Body>
                </Panel>
                <Panel>
                  <Panel.Heading><b>Art</b></Panel.Heading>
                  <Panel.Body>As a renaissance painter, Botticelli operated in a naturalistic style. His figures
                    are somewhat less anatomically correct than his contemporaries, opting instead to infuse
                    his characters with vibrant color and life. His most famous scenes include "La Primavera"
                    and "The Birth of Venus," both depicting mythological stories.
                </Panel.Body>
                </Panel>
                <Panel>
                  <Panel.Heading><b>Divine Comedy</b></Panel.Heading>
                  <Panel.Body>
                    Botticelli had a long, documented facination with Dante. As an Italian renaissance
                    artist, he looked to those who came before for inspiration - in this case, to Dante. From contemporary sources,
                    we know Botticelli was obsessed with Dante. Indeed, illustrations of the Inferno are then more personal rather
                    than commercial in nature. Botticelli's illustrations were never finished: most are still sketches, with some
                    occassional splashes of color. Nevertheless, they are revered for their beauty and technical skill.
                  </Panel.Body>
                </Panel>
              </Modal.Body>
              <Modal.Footer>
                <Button onClick={this.toggleBotticelliModal}>Close</Button>
              </Modal.Footer>
            </Modal>

          {/* DISPLAY */}
          <div id="portrait-container">
              <Thumbnail onClick={this.toggleDaliModal} href="#" alt="171x180" src={DaliPortrait} />
              <Thumbnail onClick={this.toggleBotticelliModal} href="#" alt="171x180" src={BotticelliPortrait}/>
          </div>
      </div>
    )

    }
}
